import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { CostumerService } from '../../../shared/services/costumer.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../../environments/environment';

@Component({
  templateUrl: 'view.component.html',
})
export class ViewComponent implements OnInit {
  
  customerData:any;
  customerId:any;
  baseUrl: string = '';
	
  constructor(
    private titleService: Title,
    private toastr: ToastrService,
    private costumerService:CostumerService,
    private router:Router, 
    private route:ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.titleService.setTitle("Customer View");
    this.baseUrl = environment.base_url;
    this.getCustomerInfo();
  }

  getCustomerInfo(){
  	this.route.queryParams.subscribe(param => {
      this.customerId = param.id;
    });
    
    this.costumerService.getCustomer(this.customerId).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.customerData = resData.data;
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });
  }

}
