import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CustomerUserComponent } from './customer-user.component';
import { ViewComponent } from './view/view.component';
import { EditComponent } from './edit/edit.component';
import { AddComponent } from './add/add.component';
import { CustomerInvoiceComponent } from './customer-invoice/customer-invoice.component';
import { UpdatePaymentReceivedComponent } from './update-payment-received/update-payment-received.component';

const routes: Routes = [ 
  {
    path: '',
    data: {
      title: 'Customer Users'
    },
    children: [
      {
        path: '',
        component: CustomerUserComponent,
        data: {
          title: 'List'
        }
      },
      {
        path: 'add',
        component: AddComponent,
        data: {
          title: 'Add'
        },
      },
      {
        path: 'edit',
        component: EditComponent,
        data: {
          title: 'Edit'
        },
      },
      {
        path: 'view',
        component: ViewComponent,
        data: {
          title: 'User View'
        },
      },
      {
        path: 'customer-invoice',
        component: CustomerInvoiceComponent,
        data: {
          title: 'Customer Invoice'
        },
      },
      {
        path: 'update-payment-received',
        component: UpdatePaymentReceivedComponent,
        data: {
          title: 'Update Payment Received'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CustomerUserRoutingModule { }
