import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { CostumerService } from '../../../shared/services/costumer.service';
import { Validations } from '../../../shared/validators/Validations';
import { RxwebValidators } from "@rxweb/reactive-form-validators";

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
})
export class AddComponent implements OnInit {

  addForm: FormGroup;
  submitted: boolean = false;
  checkSameBillingAddress: boolean = false;
  sales_tax_imageUrl: string = '';
  terms_and_conditionUrl: string = '';
  selectedSalesPerson: string = '';
  salesPersonList: any;

  constructor(
    private titleService: Title,
    private costumerService: CostumerService,
    private toastr: ToastrService,
    private router: Router,
    private route: ActivatedRoute
  ) {

  }
  ngOnInit(): void {
    this.titleService.setTitle("Add Customer");

    this.addForm = new FormGroup({
      store_name: new FormControl('', [
        Validators.required,
        Validators.pattern(Validations.whitespace)
      ]),
      fullname: new FormControl('', [
        Validators.required,
        Validators.pattern(Validations.whitespace)
      ]),
      phone_no: new FormControl('', [
        Validators.required
      ]),
      email: new FormControl('', [
        Validators.required,
        Validators.pattern(Validations.email_pattern)
      ]),
      billing_address: new FormControl('', [
        Validators.required
      ]),
      billing_city: new FormControl('', [
        Validators.required
      ]),
      billing_state: new FormControl('', [
        Validators.required
      ]),
      billing_zipcode: new FormControl('', [
        Validators.required
      ]),
      shipping_address: new FormControl('', [
        Validators.required
      ]),
      shipping_city: new FormControl('', [
        Validators.required
      ]),
      shipping_state: new FormControl('', [
        Validators.required
      ]),
      shipping_zipcode: new FormControl('', [
        Validators.required
      ]),
      sales_tax_id: new FormControl('', [
        //Validators.required
      ]),
      minumum_threshold: new FormControl('', [
        Validators.required
      ]),
      maximum_threshold: new FormControl('', [
        Validators.required
      ]),
      sales_tax_image: new FormControl('', [
        // Validators.required,
        // RxwebValidators.extension({extensions:["png","jpg","jpeg","emf","jpe","jfif","bmp","gif","wmf","dib","tif","tiff",'pdf']
        // })
      ]),
      sales_tax_image_Source: new FormControl('', [
        //Validators.required
      ]),
      terms_and_condition: new FormControl('', [
        // Validators.required,
        // RxwebValidators.extension({extensions:["png","jpg","jpeg","emf","jpe","jfif","bmp","gif","wmf","dib","tif","tiff",'pdf']
        // })
      ]),
      terms_and_condition_Source: new FormControl('', [
        //Validators.required
      ]),
      sales_person: new FormControl('', [Validators.required])
    });

    this.getSalesUsers();
  }

  getSalesUsers() {
    var formData = new FormData();
    formData.append("page", "1");
    formData.append("limit", "10000");

    this.costumerService.userlist(formData).subscribe(response => {
      let resData = JSON.parse(JSON.stringify(response));

      if (resData.status_code == 200) {
        this.salesPersonList = resData.data;
      } else {
        this.toastr.error('', resData.message);
      }
    },
      err => {
        this.toastr.error('', err.error.message);
      });
  }

  onTaxImgFileChange(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      if (file) {
        this.addForm.patchValue({
          sales_tax_image_Source: file
        });
        const reader = new FileReader();
        reader.onloadend = (event: any) => {
          this.sales_tax_imageUrl = event.target.result;
        };
        reader.readAsDataURL(event.target.files[0]);
      }
    }
  }

  onTermAndConditionFileChange(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      if (file) {
        this.addForm.patchValue({
          terms_and_condition_Source: file
        });
        const reader = new FileReader();
        reader.onloadend = (event: any) => {
          this.terms_and_conditionUrl = event.target.result;
        };
        reader.readAsDataURL(event.target.files[0]);
      }
    }
  }

  submitAddForm() {
    this.submitted = true;

    var formData = new FormData();
    formData.append("store_name", this.addForm.controls.store_name.value);
    formData.append("fullname", this.addForm.controls.fullname.value);
    formData.append("email", this.addForm.controls.email.value);
    formData.append("phone_no", this.addForm.controls.phone_no.value);
    formData.append("billing_address", this.addForm.controls.billing_address.value);
    formData.append("billing_state", this.addForm.controls.billing_state.value);
    formData.append("billing_city", this.addForm.controls.billing_city.value);
    formData.append("billing_zipcode", this.addForm.controls.billing_zipcode.value);
    formData.append("shipping_address", this.addForm.controls.shipping_address.value);
    formData.append("shipping_city", this.addForm.controls.shipping_city.value);
    formData.append("shipping_state", this.addForm.controls.shipping_state.value);
    formData.append("shipping_zipcode", this.addForm.controls.shipping_zipcode.value);
    formData.append("sales_tax_id", this.addForm.controls.sales_tax_id.value);
    formData.append("minumum_threshold", this.addForm.controls.minumum_threshold.value);
    formData.append("maximum_threshold", this.addForm.controls.maximum_threshold.value);
    formData.append("sales_tax_image", this.addForm.get('sales_tax_image_Source').value);
    formData.append("terms_and_condition", this.addForm.get('terms_and_condition_Source').value);
    formData.append("shipping_address_same", this.checkSameBillingAddress == false ? "False" : "True");
    formData.append("sales_person", this.addForm.controls.sales_person.value);

    if (this.addForm.valid) {
      this.costumerService.createcustomer(formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          this.router.navigate(['customer-user']).then(() => {
            this.toastr.success('', "Customer added successfully!");
          });
        } else {
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }

  changeCheckSameBillingAddress() {
    if (this.checkSameBillingAddress) {
      this.addForm.patchValue({
        shipping_address: this.addForm.controls.billing_address.value,
        shipping_city: this.addForm.controls.billing_city.value,
        shipping_state: this.addForm.controls.billing_state.value,
        shipping_zipcode: this.addForm.controls.billing_zipcode.value
      })
    } else {
      this.addForm.patchValue({
        shipping_address: "",
        shipping_city: "",
        shipping_state: "",
        shipping_zipcode: ""
      })
    }
  }

}
