import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable()
export class CostumerService {

  constructor(
    private http: HttpClient,
  ) {}

  customerlist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/customerlist/`, data);
  }

  createcustomer(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/createcustomer/`, data);
  }

  customereditprofile(data,id): Observable<any> {
    return this.http.put<any>(`${environment.base_url}/customereditprofile/${id}`, data);
  }

  getCustomer(id) : Observable<any>{
    return this.http.get<any>(`${environment.base_url}/customer/${id}/`);
  }

  deleteCustomer(id): Observable<any> {
    return this.http.delete<any>(`${environment.base_url}/deletecustomer/${id}/`);
  }

  displayorder(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/orderdisplay/displayorder/`,data);
  }

  userlist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/user/salesuser/`,data);
  }
}
